import {useContext} from 'react';
import {ScrapContext} from './ScrapContext';
export default function Data () {
    const {scraps} = useContext(ScrapContext);
    return <div>
        <h2>Your Data: </h2>
        <ul>
            {scraps.twitter.map(scrap => (
                <li key={scrap.Date}>{scrap.count}</li>
            ))}
        </ul>
    </div>
}